import { Component, h, State } from '@stencil/core';

@Component({
    tag: 'limel-example-popover-trigger-interaction',
    shadow: true,
})
export class PopoverTriggerInteractionExample {
    @State()
    private isOpen = false;

    public render() {
        return [
            <limel-popover
                triggerInteraction="hover"
                open={this.isOpen}
                onClose={this.onPopoverClose}
            >
                <limel-button
                    slot="trigger"
                    primary={true}
                    label="Click me!"
                    onClick={this.openPopover}
                />
                <p style={{ margin: '0.5rem 1rem' }} tabindex="0">
                    Content
                </p>
            </limel-popover>,
        ];
    }

    private openPopover = (event: MouseEvent) => {
        event.stopPropagation();
        console.log('opening');
        this.isOpen = true;
    };

    private onPopoverClose = (event: CustomEvent) => {
        event.stopPropagation();
        console.log('closing');
        this.isOpen = false;
    };
}
